const COMPONENT_NAME = 'TinyPagination'
import { isMobile } from 'mobile-device-detect'
export default {
  name: COMPONENT_NAME,
  props: {
    total: {
      type: Number,
      required: true
    },
    nameRecord: {
      type: String,
      default: 'hồ sơ'
    },
    page: {
      type: Number,
      default: 1
    },
    numberPerPage: {
      type: Number,
      default: 15
    },
    lang: {
      type: String,
      default: 'en'
    },
    customClass: {
      type: String
    },
    limits: {
      type: Array,
      default () {
        return [15, 30, 50, 100]
      }
    },
    showLimit: {
      type: Boolean,
      default: false
    },
    showTotal: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      version: '0.2.1',
      currentPage: 1,
      translations: {
        en: {
          prev: 'Previous',
          title: 'Page',
          next: 'Next'
        }
      },
      availableLanguages: ['en']
    }
  },
  created () {
    this.currentPage = this.page
    // console.log('total, numberPerPage', this.total, this.numberPerPage)
  },
  watch: {
    page (val) {
      this.currentPage = parseInt(val)
    }
  },
  computed: {
    translation () {
      return this.availableLanguages.includes(this.lang)
        ? this.translations[this.lang]
        : this.translations['en']
    },
    totalPages () {
      return Math.ceil(this.total / this.numberPerPage)
    },
    totalPagesData () {
      var totalDatas = []
      var data = {}
      for (var index = 1; index <= this.totalPages; ++index) {
        data = {
          value: index,
          text: 'Trang ' + index
        }
        totalDatas.push(data)
      }
      return totalDatas
    },
    titlePage () {
      return `${this.currentPage}`
    },
    classFirstPage () {
      return {
        'c-not-allowed pagination__navigation--disabled': this.currentPage === 1
      }
    },
    classLastPage () {
      return {
        'c-not-allowed pagination__navigation--disabled': this.currentPage === this.totalPages
      }
    },
    viewMobile () {
      return isMobile
    }
  },
  methods: {
    nextPage () {
      if (this.currentPage !== this.totalPages) {
        this.currentPage += 1
        this.$emit('tiny:change-page', {
          page: this.currentPage,
          numberPerPage: this.numberPerPage
        })
      }
    },
    lastPage () {
      if (this.currentPage > 1) {
        this.currentPage -= 1
        this.$emit('tiny:change-page', {
          page: this.currentPage,
          numberPerPage: this.numberPerPage
        })
      }
    },
    nextPageLast () {
      this.currentPage = this.totalPages
      this.$emit('tiny:change-page', {
        page: this.totalPages,
        numberPerPage: this.numberPerPage
      })
    },
    lastPageLast () {
      this.currentPage = 1
      this.$emit('tiny:change-page', {
        page: 1,
        numberPerPage: this.numberPerPage
      })
    },
    goToPage () {
      this.$emit('tiny:change-page', {
        page: this.currentPage,
        numberPerPage: this.numberPerPage
      })
    },
    onLimitChange () {
      this.currentPage = 1
      this.$emit('tiny:change-page', {
        page: 1,
        numberPerPage: this.numberPerPage
      })
    }
  }
}
