    var vm = this;
    vm.page = 1;
    vm.$nextTick(function () {});
  },
  watch: {
    applicantBussinessExit: function applicantBussinessExit(val) {
      var vm = this;

      if (val && vm.fileTemplateNoString) {
        vm.getDossierFileApplicants(val, vm.fileTemplateNoString);
      }
    },
    applicantId: function applicantId(val) {
      var vm = this;

      if (val && vm.fileTemplateNoString) {
        vm.getDossierFileApplicants(val, vm.fileTemplateNoString);
