        var referenceUid = vm.thongTinHoSo.referenceUid;
        var dossierStatus = vm.thongTinHoSo.dossierStatus;
        var dossierSubStatus = vm.thongTinHoSo.dossierSubStatus;
        var templateNo = item.partNo;
        var deliverableType = item.deliverableType ? item.deliverableType : '';
        var token = localStorage.getItem('jwt_token');
        var urlEmbed = eformScript.eformEmbed + '/' + item.fileTemplateNo + '/referenceUid/' + referenceUid + '/' + token + '/' + encodeURIComponent(document.location.origin) + '?userId=' + userId + '&userEmail=' + userEmail + '&code=' + referenceUid + '&dossierStatus=' + dossierStatus + '&dossierSubStatus=' + dossierSubStatus + '&tp=' + templateNo; // console.log('urlEmbed', urlEmbed)

        setTimeout(function () {
          document.getElementById('formAlpaca' + item.partNo + vm.id).src = urlEmbed;
        }, 300);
      } else {
        item.embed = false;
        setTimeout(function () {
          var fileFind = vm.dossierFilesItems.find(function (itemFile) {
            return itemFile.dossierPartNo === item.partNo && itemFile.eForm;
          });
