export default {
  props: {
    user_note: {
      type: Number,
      required: true
    },
    configNote: {
      type: Object,
      required: false
    }
  },
  data: () => ({
    noteYkien: '',
    valid: false
  }),
  methods: {
    initData (data) {
      var vm = this
      vm.$store.dispatch('getDetailDossier', data).then(resultDossier => {
        vm.thongTinChiTietHoSo = resultDossier
      })
    },
    doExport () {
      let vm = this
      let exportData = {
        text: vm.noteYkien,
        valid: vm.$refs.userNoteForm.validate()
      }
      return exportData
    }
  }
}
