import Vue from 'vue';
import axios from 'axios';
export default {
  props: {
    payments: {
      type: Object,
      default: function _default() {}
    },
    dossierDetail: {
      type: Object,
      default: function _default() {}
    }
  },
  components: {},
  data: function data() {
    return {
      loading: true,
      loadingAction: false,
      paymentFile: '',
      dialogPDF: false,
      dialogPDFLoading: true,
      activePrintPay: false,
      transId: '',
      goodCode: '',
      doneVTpay: false
    };
  },
  computed: {
    paymentFileName: function paymentFileName() {
      return this.$store.getters.getPaymentFileName;
    },
    originality: function originality() {
      var vm = this;
      return vm.getOriginality();
    },
    ePaymentProfile: function ePaymentProfile() {
      var vm = this;
      return vm.getEPaymentProfile(vm.payments['epaymentProfile']);
    },
    eInvoiceInfo: function eInvoiceInfo() {
      var vm = this;

      try {
        return JSON.parse(vm.payments['einvoice']);
      } catch (error) {
        console.log('Không có thông tin hóa đơn - einvoice');
        return '';
      }
    }
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
    });
  },
  watch: {
    dossierDetail: function dossierDetail(val) {
      var vm = this;
      vm.getPaymentFiles();
    },
    payments: function payments(val) {
      // lấy thông tin tra cứu trên keypay
      var vm = this; // if (!vm.paymentFile && val['paymentMethod'] === 'Chuyển khoản') {
      //   vm.payments.paymentMethod = 'Keypay'
      // }

      var paymentProfile = vm.getEPaymentProfile(val.epaymentProfile); // keypay

      if (paymentProfile && paymentProfile['keypayUrl']) {
        vm.transId = paymentProfile['keypayUrl'].split('&').filter(function (item) {
          return item.indexOf('merchant_trans_id') >= 0;
        })[0].split('=')[1];
        vm.goodCode = paymentProfile['keypayUrl'].split('&').filter(function (item) {
          return item.indexOf('good_code') >= 0;
        })[0].split('=')[1];
      } // viettelPay


      if (paymentProfile && paymentProfile.genQRCode && String(val.paymentStatus) !== '3' && String(val.paymentStatus) !== '5') {
        var filter = {
          billcode: paymentProfile.genQRCode.BILLCODE,
          order_id: paymentProfile.genQRCode.ORDER_ID
        };
        vm.$store.dispatch('getVtPayStatus', filter).then(function (result) {// vm.doneVTpay = true
        }).catch(function () {});
      }
    }
  },
  methods: {
    getPaymentFiles: function getPaymentFiles() {
      var vm = this;
      var filter = vm.dossierDetail;
      vm.$store.dispatch('getPaymentFiles', filter).then(function (result) {
        vm.paymentFile = result;
      }).catch(function () {});
    },
    currency: function currency(value) {
      if (value) {
        var moneyCur = (value / 1).toFixed(0).replace('.', ',');
        return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
      }

      return '';
    },
    viewFile: function viewFile() {
      var vm = this;
      vm.activePrintPay = false;
      vm.dialogPDFLoading = true;
      vm.dialogPDF = true;
      var filter = {
        dossierId: vm.dossierDetail.dossierId,
        referenceUid: vm.dossierDetail.referenceUid
      };
      vm.$store.dispatch('viewPaymentFile', filter).then(function (result) {
        vm.dialogPDFLoading = false;
        document.getElementById('dialogPaymentPreview').src = result;
      }).catch(function () {});
    },
    getEPaymentProfile: function getEPaymentProfile(paymentProfile) {
      if (paymentProfile) {
        try {
          JSON.parse(paymentProfile);
          return JSON.parse(paymentProfile);
        } catch (e) {
          return '';
        }
      } else {
        return '';
      }
    },
    downloadPaymentFile: function downloadPaymentFile(item) {
      var vm = this;
      var url = '/o/rest/v2/dossiers/' + vm.dossierDetail.dossierId + '/payments/' + vm.dossierDetail.referenceUid + '/confirmfile';
      window.open(url);
    },
    printPay: function printPay() {
      var vm = this;
      vm.activePrintPay = true;
      var filter = {
        dossierId: vm.dossierDetail.dossierId,
        referenceUid: vm.payments.referenceUid
      };
      vm.dialogPDFLoading = true;
      vm.dialogPDF = true;

      if (vm.payments['paymentMethod'] === 'KeyPayDVCQG') {
        var confirmPayload = vm.payments.hasOwnProperty('confirmPayload') ? vm.getEPaymentProfile(vm.payments['confirmPayload']) : '';
        vm.dialogPDFLoading = false;
        var url = confirmPayload ? confirmPayload['url_invoice'] : '';

        if (url) {
          window.open(url, "_blank");
        } else {
          alert('Không có biên lai');
        }
      } else if (vm.payments['paymentMethod'] === 'PayPlatDVCQG') {
        // let param = {
        //   headers: {
        //     groupId: window.themeDisplay.getScopeGroupId()
        //   },
        //   responseType: 'blob'
        // }
        // axios.get('/o/rest/v2/dossiers/'+ filter.dossierId + '/payments/' + filter.referenceUid + '/invoice', param).then(function (response) {
        //   vm.dialogPDFLoading = false
        //   let serializable = response.data
        //   let file = window.URL.createObjectURL(serializable)
        //   document.getElementById('dialogPaymentPreview').src = file
        // }).catch(function (error) {
        //   vm.dialogPDFLoading = false
        // })
        vm.dialogPDFLoading = false;
        vm.dialogPDF = false;

        var _url = '/o/rest/v2/dossiers/' + filter.dossierId + '/payments/' + filter.referenceUid + '/invoice';

        window.open(_url, "_blank");
      } else {
        vm.$store.dispatch('printPay', filter).then(function (result) {
          vm.dialogPDFLoading = false;
          document.getElementById('dialogPaymentPreview').src = result;
        }).catch(function () {});
      }
    },
    getEinvoiceNo: function getEinvoiceNo(string) {
      if (string && string.indexOf('#') >= 0) {
        var eInvoiceNo = string.split('#')[1] ? string.split('#')[1] : '';
        return eInvoiceNo.indexOf('}') < 0 && eInvoiceNo.indexOf('"') < 0 ? eInvoiceNo : '';
      } else {
        return '';
      }
    },
    getEinvoiceCode: function getEinvoiceCode(string) {
      if (string && string.indexOf('#') >= 0) {
        var eInvoiceCode = string.split('#')[3] ? string.split('#')[3].split(',')[0].replace('"', '') : '';
        return eInvoiceCode;
      } else {
        return '';
      }
    },
    tracuuhoadon: function tracuuhoadon() {
      var vm = this;
      var urlRedirect;

      if (vm.eInvoiceInfo && vm.eInvoiceInfo['supplierTaxCode']) {
        urlRedirect = vm.eInvoiceInfo['sInvoiceUrl'];
      } else {
        urlRedirect = 'http://tracuu.cmcsoft.com';
      }

      window.open(urlRedirect, '_blank');
    },
    checkVTpay: function checkVTpay() {
      var vm = this;
      var paymentProfile = vm.getEPaymentProfile(vm.payments.epaymentProfile);
      var filter = {
        billcode: paymentProfile.genQRCode.BILLCODE,
        order_id: paymentProfile.genQRCode.ORDER_ID
      };
      vm.$store.dispatch('getVtPayStatus', filter).then(function (result) {}).catch(function () {});
    },
    getPaymentStatus: function getPaymentStatus(code) {
      if (code === 1 || code === '1') {
        return 'Yêu cầu nộp tiền tạm ứng ';
      } else if (code === 2 || code === '2') {
        return 'Yêu cầu quyết toán phí';
      } else if (code === 3 || code === '3') {
        return 'Báo đã nộp phí trực tuyến';
      } else if (code === 4 || code === '4') {
        return 'Yêu cầu nộp lại phí trực tuyến';
      } else if (code === 5 || code === '5') {
        return 'Đã hoàn thành thu phí';
      } else {
        return '';
      }
    }
  }
};